<template>
  <v-container>
    <v-app-bar fixed>
      <v-col class="text-left">
        <h4 class="text-justify font-weight-bold"> Almost there </h4>
      </v-col>
      <v-chip color="info" v-if="(breakpoint==='md'||breakpoint==='lg'||breakpoint==='xl')">
        <h4>Question {{cId + 1}} of {{questions.length}}</h4>
      </v-chip>
      <v-chip color="info" v-if="(breakpoint==='xs'||breakpoint==='sm')">
        <h4>Q. {{cId + 1}} of {{questions.length}}</h4>
      </v-chip>
      <v-col class="text-right">
        <v-btn
          depressed
          v-if="(breakpoint==='xs'||breakpoint==='sm')"
          :disabled="!activate"
          color="success"
          value="right"
          v-on:click="submit"
        >
          <v-icon small dark>mdi-check-bold</v-icon> <v-spacer></v-spacer> Submit
        </v-btn>
<!--        <v-btn-->
<!--          v-else-if="!activate"-->
<!--          color="success"-->
<!--          value="right"-->
<!--          v-on:click="incrementSlide"-->
<!--        >-->
<!--          Next <v-icon small dark>mdi-forward</v-icon> <v-spacer></v-spacer>-->
<!--        </v-btn>-->
      </v-col>
    </v-app-bar>
    <v-row align="center" justify="center" class="mt-16">
      <v-carousel light :show-arrows="false" :hide-delimiters="hideDelimiters" :cycle="false" :continuous="false" :hide-delimiter-background="true" v-model="cId">
        <v-form ref="form" v-model="valid" v-on:submit.prevent="submit">
          <v-carousel-item v-for="(question, i) in questions" :key="`${i}-${question.id}`">
            <v-row justify="center" align="center">
              <v-card class="ma-md-4 pa-md-4 ma-4 pa-4" scrollable style="width: 850px;" outlined tile>
                <h5 class="text-justify grey--text text--darken-2">Question</h5>
                <h3 class="text-justify">{{ question.text }}</h3>
                <v-rad ref="radio" v-if="question.type==='radio'" :question="question" @recordChoice="recordChoice"></v-rad>
                <v-range ref="radio" v-if="question.type==='range_slider'" :question="question"  @recordChoice="recordRangeChoice"></v-range>
                <v-multi-chip ref="multi_chip_select" v-if="question.type==='checkbox'" @recordChoice="recordChoice" :question="question"></v-multi-chip>
                <br>
                <v-row justify="center">
                  <v-btn depressed color="info" v-on:click="decrementSlide" :disabled="cId===0">
                    <v-icon small dark>mdi-arrow-left-thick</v-icon> previous
                  </v-btn>
                  <v-btn depressed color="warning" style="margin-left: 20px" v-on:click="incrementSlide" :disabled="cId===questions.length-1">
                    next <v-icon small dark>mdi-arrow-right-thick</v-icon>
                  </v-btn>
                  <v-btn
                    depressed
                    v-if="(breakpoint==='md'||breakpoint==='lg'||breakpoint==='xl')"
                    :disabled="!activate"
                    style="margin-left: 20px"
                    color="success"
                    v-on:click="submit"
                  >
                    <v-icon small dark>mdi-check-bold</v-icon> <v-spacer></v-spacer> Submit
                  </v-btn>
                </v-row>
              </v-card>
            </v-row>
          </v-carousel-item>

        </v-form>
      </v-carousel>
    </v-row>
    <v-overlay :value="overlay">
      <v-row style="flex-direction: column" justify="center" align="center">
        <v-progress-circular indeterminate size="48"></v-progress-circular>
        {{ overlayMessage }}
      </v-row>
    </v-overlay>
    <v-snack ref="snack"></v-snack>
  </v-container>
</template>

<script>
  import SliderQuestions from "../Questions/SliderQuestions";
  import Snackbar from "../UI/Snackbar";
  import Radio from "../ResponseTypes/Radio";
  import MultiChipSelect from "../ResponseTypes/MultiChipSelect";
  import RangeSlider from "../ResponseTypes/RangeSlider";
  export default {
      name: "IdeologicalQ",
      components: {
          'v-rad': Radio,
          'v-multi-chip': MultiChipSelect,
          'v-snack': Snackbar,
          'v-range': RangeSlider
      },
      data () {
          return {
              cId: 0,
              msg: "",
              completedQuestions: [],
              responses: {},
              breakpoint: '',
              overlay: false,
              activate: false,
              overlayMessage: '',
              questions: [],
              valid: false,
          }
      },
      beforeMount() {
          this.overlay = true;
          this.overlayMessage = 'Getting data ... ';
          this.$store.dispatch('loadSurveyFromID', 2).then(
              response => {
                  this.overlay = false;
                  this.questions = this.parseQuestions(response.survey.questions);
              },
              error => {
                  this.overlay = false;
                  this.$refs.snack.showSnackbar('error', 'Got nothing from server. Please check your internet connection or refresh the page.');
              }
          );
          this.breakpoint = this.$vuetify.breakpoint.name;
          this.$vuetify.goTo(0);

      },
      beforeDestroy () {
         clearTimeout(this.timeout)
      },
      computed: {
          style() {
              switch (this.$vuetify.breakpoint.name) {
                case 'xs': return 'height: 750px';
                case 'sm': return 'height: 750px';
                case 'md': return 'height: 600px';
                case 'lg': return 'height: 600px';
                case 'xl': return 'height: 600px';
              }
          },
          hideDelimiters() {
              switch (this.$vuetify.breakpoint.name) {
                case 'xs': return true;
                case 'sm': return true;
                case 'md': return true;
                case 'lg': return true;
                case 'xl': return true;
              }
          }
      },
      watch: {
          completedQuestions(newValue) {
              console.log(newValue.length);
              if (newValue.length === this.questions.length - 1) {
                  this.activate = true;
              }
          }
      },
      methods: {
          validate: function () {
              return this.$refs.form.validate()
          },
          parseQuestions: function(questions) {
              // console.log(questions);
              for (let i = 0; i < questions.length; i++) {
                  let choices = questions[i].simple_choices;
                  questions[i].simple_choices = _.orderBy(choices, 'id')
              }
              return questions;
          },
          submit: function() {
              let followed_news_outlets = [];
              for (let i = 0; i < this.responses.followed_news_outlets.length; i++) {
                  if (typeof this.responses.followed_news_outlets[i] === "string" ) {
                      followed_news_outlets.push(this.responses.followed_news_outlets[i])
                  } else {
                      followed_news_outlets.push(this.responses.followed_news_outlets[i].id)
                  }
              }
              if (typeof this.responses.political_ideology === "undefined") {
                  this.responses.political_ideology = 0;
              }
              this.overlay = true;
              this.overlayMessage = 'Submitting your response ... ';
              this.$store.dispatch('update_ideology_info', {
                  survey_record_id: localStorage.media_bias_lexica_annotator_survey_record_id_key,
                  political_ideology: this.responses.political_ideology,
                  news_check_frequency: this.responses.news_check_frequency[0].id,
                  followed_news_outlets: followed_news_outlets.toString(),
              }).then(response => {
                  this.overlay = false;
                  this.handleResponse(response);
              }, error => {
                  this.overlay = false;
                  this.$refs.snack.showSnackbar('error', 'Got nothing from server. Please check your internet connection or refresh the page.');
              })
          },
          handleResponse: function (response) {
              this.$router.replace({name: 'IQC'});
          },
          recordChoice: function(question, choice) {
              if (!this.completedQuestions.includes(question)) {
                  this.completedQuestions.push(question);
                  this.responses[question.name] = [choice];
              }
              else {
                  if  (typeof this.responses[question.name] !== "undefined") {
                      this.responses[question.name].push(choice);
                  }
              }
          },
          recordRangeChoice: function(question, choice) {
              if (!this.completedQuestions.includes(question)) {
                  this.responses[question.name] = choice;
              }
              else {
                  this.responses[question.name] = choice
              }
          },
          incrementSlide: function () {
              setTimeout(() => {
                  if (this.cId < this.questions.length - 1) {
                      this.cId++;
                  }
              }, 0)
          },
          decrementSlide: function () {
              setTimeout(() => {
                  if (this.cId > 0) {
                      this.cId--;
                  }
              }, 0)
          },
      }
  }
</script>

<style scoped>

</style>



