<template>
    <div>
<!--      <div style="height: 20px">-->
<!--          <v-slide-y-transition>-->
<!--          <h5 class="text-justify green&#45;&#45;text text&#45;&#45;darken-1" v-show="!hintHidden">Please slide to record your response.</h5>-->
<!--        </v-slide-y-transition>-->
<!--      </div>-->
      <h5 class="text-justify">Please slide to record your response.</h5>
      <v-row v-if="breakpoint==='xs' || breakpoint==='sm'" style="flex-direction: column">
        <h4 class="my-2 grey--text text--darken-1"> {{ question['range_slider_choices'][0]['label_right_side'] }} </h4>
        <v-slider
          v-model="slider"
          color="primary"
          :min="question['range_slider_choices'][0]['min_range']"
          :max="question['range_slider_choices'][0]['max_range']"
          thumb-label="always"
          vertical
          thumb-size="40"
          v-on:change="recordResponse(slider)"
        ></v-slider>
        <h4 class="my-2 grey--text text--darken-1"> {{ question['range_slider_choices'][0]['label_left_side'] }} </h4>
      </v-row>
      <v-row class="ma-0 mt-8" v-else-if="breakpoint==='lg' || breakpoint==='md' || breakpoint==='xl'" style="flex-direction: row">
        <h4 class="my-8 grey--text text--darken-1"> {{ question['range_slider_choices'][0]['label_left_side'] }} </h4>
        <v-slider
          class="ma-8"
          v-model="slider"
          color="primary"
          :min="question['range_slider_choices'][0]['min_range']"
          :max="question['range_slider_choices'][0]['max_range']"
          thumb-label="always"
          v-on:change="recordResponse(slider)"
        ></v-slider>
        <h4 class="my-8 grey--text text--darken-1"> {{ question['range_slider_choices'][0]['label_right_side'] }} </h4>
      </v-row>
    </div>
</template>

<script>
    export default {
        name: "RangeSlider",
        data() {
            return {
                breakpoint: '',
                hintHidden: false,
                slider: 0
            }
        },
        mounted() {
            this.breakpoint = this.$vuetify.breakpoint.name;
            console.log(this.question);
        },
        props: {
            question: Object,
        },
        methods: {
            recordResponse: function (slider) {
                this.hintHidden = true;
                this.$emit('recordChoice', this.question, slider);
            }
        }
    }
</script>

<style scoped>

</style>



