<template>
  <v-container fluid fill-height>
    <v-app-bar fixed>
      <v-col class="text-left">
        <h4 class="text-justify font-weight-bold">Annotation task</h4>
      </v-col>
      <v-chip color="info" v-if="(breakpoint==='md'||breakpoint==='lg'||breakpoint==='xl')">
        <h4>Sentence {{index + 1}} of {{sentences.length}}</h4>
      </v-chip>
      <v-chip color="info" v-if="(breakpoint==='xs'||breakpoint==='sm')">
        <h4>{{index + 1}} of {{sentences.length}}</h4>
      </v-chip>
      <v-col class="text-right">
        <v-btn
          depressed
          :disabled="!valid"
          color="success"
          value="right"
          v-on:click="submit"
        >
          next <v-icon small dark>mdi-arrow-right-thick</v-icon> <v-spacer></v-spacer>
        </v-btn>
      </v-col>
    </v-app-bar>
    <v-row align="center" justify="center" style="margin-top: 48px; flex-direction: column">
      <v-card class="pa-6 ma-md-12 ma-lg-12 ma-xs-12 ma-sm-12" style="width: 600px" outlined tile>
        <h3 v-html="currentSentence.text" class="text-justify font-weight-bold font-italic" style="margin: 12px; font-size: larger" id="sentence"></h3>
        <v-form ref="form" v-model="valid">
          <v-col cols="12" v-for="(question, i) in questions" :key="`${i}-${question.id}`">
            <h5 class="text-justify grey--text text--darken-2">Question</h5>
            <h3 class="text-justify">{{ question.text }}</h3>
            <h5 class="text-justify" v-if="question.type==='radio'">Please select one option.</h5>
            <h5 class="text-justify" v-if="question.type==='checkbox'">You can select more than one option.</h5>
            <h5 class="text-justify" v-if="question.type==='highlight'">
              Instructions: <br>
              1. Use your mouse pointer/finger to select the text. <br>
              2. It is possible to highlight both words and short phrases, such as, illegal aliens. <br>
              3. Once you are done selecting, your selection will be automatically displayed below. <br>
              4. Please <strong class="red--text">do not select</strong> long phrases at a time. <br>
              5. In case you want to re-visit the information we've provided before,
              <v-btn
                color="info"
                depressed
                x-small
                v-on:click="showInfoModal"
              >
                click here
              </v-btn>
            </h5>
            <v-radio-group
                v-model="radioGroup.selection[question.name]"
                :rules="radioRules"
                v-if="question.type==='radio'"
              >
                <v-radio
                  v-for="choice in question.simple_choices"
                  :key="`choice-${choice.id}`"
                  :label="choice.text"
                  :value="choice.id"
                  color="success"
                ></v-radio>
              </v-radio-group>
            <v-row
              justify="start" align="center"
              v-else-if="question.type==='checkbox'"
              class="ma-md-0 ma-0"
            >
              <v-chip-group
                column
                multiple
                mandatory
              >
                <v-chip
                  v-for="choice in question.choices"
                  v-model="checkBoxGroup.selection[choice.id]"
                  :key="`choice-${choice.id}`"
                  :value="choice.id"
                  color="success"
                  filter
                  outlined
                >
                  {{ choice.text }}
                </v-chip>
              </v-chip-group>
            </v-row>
            <v-row justify="start" align="center" v-else-if="question.type==='highlight'">
              <br>
              <v-chip
                  v-for="(annotation, index) in annotations"
                  :key="`annotation-${index}`"
                  v-model="chipGroup.chips[index]"
                  class="ma-2"
                  close
                  filled
                  color="success"
                  @click:close="removeFromAnswer(annotation, index)"
                >
                  {{ annotation}}
              </v-chip>
            </v-row>
          </v-col>
        </v-form>
      </v-card>
    </v-row>
    <v-overlay :value="overlay">
      <v-row style="flex-direction: column" justify="center" align="center">
        <v-progress-circular indeterminate size="48"></v-progress-circular>
        {{ overlayMessage }}
      </v-row>
    </v-overlay>
    <v-snack ref="snack"></v-snack>
    <v-dialog ref="dialog"></v-dialog>
    <info-modal ref="infoModal"></info-modal>
  </v-container>
</template>

<script>
    import Snackbar from "../UI/Snackbar";
    import Dialog from "../UI/Dialog";
    import InstructionsModal from "../UI/InstructionsModal";
    export default {
        name: "Annotation",
        components: {
            'v-snack': Snackbar,
            'v-dialog': Dialog,
            'info-modal': InstructionsModal,
        },
        data() {
            return {
                nonBiasedOptionLocal: 37,
                nonBiasedOptionProd: 49,
                overlay: false,
                overlayMessage: '',
                valid: false,
                msg: "",
                index: 0,
                selected: '',
                questions: [],
                sentences: [],
                currentSentence: '',
                cId: 0,
                checkBoxGroup:{
                    selection: [],
                },
                radioGroup:{
                    selection: [],
                },
                chipGroup:{
                    chips: [],
                },
                radioRules: [
                  v => !!v || 'It is required'
                ],
                annotations: [],
                breakpoint: '',
                selectionEndTimeout: '',
                sentenceLength: 0,
            }
        },
        mounted() {
            this.reset();
            this.addListeners();
        },
        beforeMount() {
            this.overlay = true;
            this.overlayMessage = 'Fetching data ...';
            this.$store.dispatch('loadSurveyFromID', 4).then(
                response => {
                    this.questions = this.parseQuestions(response.survey.questions);
                    this.$store.dispatch('loadRandomSurveySentences').then(
                        response => {
                            this.overlay = false;
                            this.sentences = response.survey_sentences;
                            this.currentSentence = this.sentences[this.index];
                        },
                        error => {
                            this.overlay = false;
                            this.$refs.snack.showSnackbar('error', 'Got nothing from server. Please check your internet connection or refresh the page.');
                        }
                    )
                },
                error => {
                    this.$refs.snack.showSnackbar('error', 'Got nothing from server. Please check your internet connection or refresh the page.');
                }
            );
            this.breakpoint = this.$vuetify.breakpoint.name;
        },
        computed: {

        },
        methods: {
            addListeners: function() {
              document.addEventListener('selectionchange', () => {
                  if (this.selectionEndTimeout) {
                      clearTimeout(this.selectionEndTimeout);
                  }
                  this.selectionEndTimeout = setTimeout(function () {
                      var event = new Event('selectionEnd');
                      document.dispatchEvent(event);
                  }, 1000);
              });

              document.addEventListener("selectionEnd", () => {
                  this.selectionEndTimeout = null;
                  if (window.getSelection().focusNode.parentElement.id === 'sentence') {
                      if (window.getSelection) {
                          this.addToAnswer(window.getSelection().toString());
                      } else if (document.selection && document.selection.type !== "Control") {
                          this.addToAnswer(document.selection.createRange().text);
                      }
                  }
              });
            },
            parseQuestions: function(questions) {
                for (let i = 0; i < questions.length; i++) {
                    let choices = questions[i].choices;
                    questions[i].choices = _.orderBy(choices, 'id')
                }
                return questions;
            },
            validate: function () {
                return this.$refs.form.validate()
            },
            incrementSlide: function () {
                setTimeout(() => {
                    if (this.cId < this.questions.length - 1) {
                        this.cId++;
                    }
                }, 500)
            },
            submit: function () {
                if (this.validate()) {
                    // console.log(this.radioGroup.selection['sentence_bias_label']);
                    if (this.annotations.length===0 && this.radioGroup.selection['sentence_bias_label']!==this.nonBiasedOptionProd) {
                        this.$refs.dialog.showErrorDialog('You forgot something...', 'You have labelled the sentence as "biased" but did not annotate any word or phrase, which is required.')
                        return;
                    }
                    this.overlay = true;
                    this.overlayMessage = 'Submitting your response ...';
                    this.$store.dispatch('annotate_sentence', {
                        survey_record_id: localStorage.media_bias_lexica_annotator_survey_record_id_key,
                        survey_sentence_id: this.currentSentence.id,
                        group_id: this.currentSentence.group_id,
                        label: this.radioGroup.selection['sentence_bias_label'],
                        words: this.annotations.toString(),
                        factual: this.radioGroup.selection['sentence_opinion_fact'],
                    }).then(response => {
                        this.overlay = false;
                        this.handleResponse(response);
                    }, error => {
                        this.overlay = false;
                        this.$refs.snack.showSnackbar('error', 'Got nothing from server. Please check your internet connection or refresh the page.');
                    });
                }
            },
            addToAnswer: function (text) {
                if (text.length >= (this.currentSentence.text.length / 4)) {
                    this.$refs.snack.showSnackbar('error', 'Selection length exceeded. Please try to make your selection shorter.');
                }
                else if (text.split(' ').length >= 6) {
                    this.$refs.snack.showSnackbar('error', 'Selection length exceeded. Please select less than 6 words.');
                }
                else if (text.length >= 2) {
                    this.annotations.push(text);
                    this.highlight(text);
                }
            },
            removeFromAnswer: function(annotation, index) {
                // console.log(annotation);
                this.annotations.splice(index, 1);
                this.unhighlight(annotation);
            },
            highlight: function(text) {
                var spnt = "<mark>" + text + "</mark>";
                var inputText = document.getElementById("sentence");
                var innerHTML = inputText.innerHTML;
                var index = innerHTML.indexOf(text);
                if (index >= 0) {
                   // innerHTML = innerHTML.substring(0,index) + "<span class='highlighted'>" + innerHTML.substring(index,index+text.length) + "</span>" + innerHTML.substring(index + text.length);
                    var x = innerHTML.replace(text, spnt);
                    inputText.innerHTML = x;
                }
            },
            handleResponse: function (response) {
                  this.reset();
                  if (this.index === this.sentences.length - 1) {
                      this.$router.push({name: 'EndSurvey'});
                  } else {
                      this.index++;
                      this.currentSentence = this.sentences[this.index];
                  }
            },
            reset: function() {
                this.annotations = [];
                this.$vuetify.goTo(0);
                this.chipGroup.chips = [];
                this.$refs.form.reset();
            },
            unhighlight: function(text) {
                var rgt = /text/;
                var spnt = "<mark>";
                var inputText = document.getElementById("sentence");
                var innerHTML = inputText.innerHTML;
                var index = innerHTML.indexOf(text);
                // console.log(index);
                if (index >= 0) {
                   innerHTML = innerHTML.substring(0,index-spnt.length) + innerHTML.substring(index,index+text.length) + innerHTML.substring(index + text.length);
                   inputText.innerHTML = innerHTML;
                }
            },
            showInfoModal: function () {
                this.$refs.infoModal.show()
            }
        }
    }
</script>

<style scoped>

</style>



