<template>
  <div id="app">
    <v-app>
      <router-view/>
    </v-app>
  </div>
</template>

<script>
  import Dialog from "./components/UI/Dialog";
  import BaseTimer from "./components/UI/BaseTimer";
  export default {
      name: 'App',
      components: {'dialog': Dialog, 'base-timer': BaseTimer},
      beforeMount() {
          window.addEventListener("beforeunload", this.preventNav);
          this.$once("hook:beforeDestroy", () => {
              window.removeEventListener("beforeunload", this.preventNav);
          })
      },
      beforeRouteLeave(to, from, next) {
          const answer = window.confirm('Do you really want to leave? You have unsaved changes!');
          if (answer) {
              next()
          } else {
              next(false)
          }
      },
      methods: {
          preventNav(event) {
              let surveyCompleted = localStorage.media_bias_lexica_annotator_survey_completed_by_user;
              if (surveyCompleted) return;
              event.preventDefault();
              event.returnValue = "";
          },
      },
      created() {
          // Set a 30 minute time-out
          // let self = this;
          // let timeout;
          // function refresh() {
          //     clearTimeout(timeout);
          //     timeout = setTimeout(() => {
          //       self.$store.dispatch('removeUserFromPool', {
          //           survey_record_id: localStorage.media_bias_lexica_annotator_survey_record_id_key,
          //       }).then(
          //           response => {
          //               self.$router.replace({name: 'Welcome', query: {redirectFrom: 'timeout', message: response.message}});
          //               localStorage.media_bias_lexica_annotator_timeout = true
          //           },
          //           error => {
          //               console.log(error);
          //               // localStorage.media_bias_lexica_annotator_timeout = false
          //           }
          //       )
          //     }, 30 * 60 * 1000)
          // }
          // refresh()
      }
  }
</script>

<style>
#app {
  font-family: "Source Sans Pro", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  background-color: #E0F2F1;
}
</style>



