<template>
  <v-container fluid fill-height>
    <v-row align="center" justify="center">
      <v-card class="pa-6 ma-md-12 ma-lg-12 ma-xs-0 ma-sm-0" style="width: 800px" outlined tile>
        <h4 class="text-justify font-weight-black">
          The way in which the news media report on current events, e.g., which words they choose to describe the same events, influences how we as readers perceive political and societal issues. In this survey we will show you a selection of sentences from various media articles on different issues and ask you to annotate media bias instances according to our detailed instructions.
        </h4>
        <br>
        <h2 class="font-weight-black">
            Why do we collect and use your data?
        </h2>
        <br>
        <h4 class="text-justify font-weight-regular">
          You are invited to participate in a research study that is being conducted by a group of researchers. <strong class="font-weight-black">The purpose of this study is to annotate media bias instances</strong>.
          This study is <strong class="font-weight-black">anonymous</strong>. Anonymous means that we will not record any information about you that could identify you personally. There will be no linkage between your identity and your responses. This means that we will not record your name, address, phone number, date of birth, etc. The research team is the only party allowed to see your data, except as may be required by law or academic journal standards. Several hundred people will participate in this study. <strong class="font-weight-black">All study data will be kept for three years</strong>.
          There are no foreseeable risks to participate in this study. In addition, you may receive a direct benefit from taking part in this study.
          The survey will take about <strong class="font-weight-black">15 - 20 minutes</strong> and you will receive <strong class="font-weight-black">$2.5 as compensation</strong>. One of the tasks will be to <strong>highlight biased words</strong> in the sentences that we will show. Dont worry if it is not clear now  <strong>we will provide detailed instructions later</strong>. For validation, several people will annotate the same sentences. Once the survey is complete, <strong class="font-weight-black">you will be shown a unique code that you can enter in the MTurk form</strong>.
          Participation in this study is voluntary. You may choose not to participate and you may withdraw at any time during the study without any penalty to you. If you have any questions about the study, or study procedures, you may contact <strong class="font-weight-black">John Doe</strong> under john.doe@internet.com.
        </h4>
<!--        <h4 class="font-weight-regular text-justify green&#45;&#45;text pa-6 pa-xs-0 pa-sm-2" style="border-style: solid; border-width: 2px">-->
<!--          Since your attention and careful annotation according to the instructions is crucial for the results of the annotation and research in general, we encourage you with the <strong>bonus payment of $0.02 for each word that was annotated by at least one other person but withdraw $0.01 for each word that was not annotated by anybody else</strong>.-->
<!--        </h4>-->
<!--        <v-card outlined tile>-->
<!--          <h4 class="text-justify font-weight-bold ma-4">-->
<!--            Since your attention and careful annotation according to the instructions is crucial for the results of the annotation and research in general, we encourage you with the <strong>bonus payment of $0.02 for each word that was annotated by at least one other person but withdraw $0.01 for each word that was not annotated by anybody else</strong>.-->
<!--          </h4>-->
<!--        </v-card>-->
        <br>
        <h4 class="text-justify font-weight-regular">
          We will first ask you to provide some simple general background information as well as information on your personal views and media usage. The main part of the survey will then ask you to annotate media bias instances according to our detailed instructions.
        </h4>
        <v-form v-model="valid" ref="form">
          <v-switch
            color="green"
            v-model="checkbox"
            :rules="[v => !!v || 'You must agree to continue!']"
            label="I agree to the processing of my personal data in accordance with the information provided herein"
            required
          ></v-switch>
        </v-form>
        <br>
        <br>
        <v-btn
          depressed
          :disabled="!valid"
          color="success"
          dark
          value="right"
          large
          @click="submit"
        >
          Start survey
        </v-btn>

      </v-card>
    </v-row>
    <v-overlay :value="overlay">
      <v-row style="flex-direction: column" justify="center" align="center">
        <v-progress-circular indeterminate size="48"></v-progress-circular>
        {{ overlayMessage }}
      </v-row>
    </v-overlay>
    <v-snack
      ref="snack"
    >
    </v-snack>
  </v-container>
</template>

<script>
  import axios from 'axios';
  import Snackbar from "../UI/Snackbar";
  const BASE_API_URL = process.env.API_URL;
    export default {
        name: "DataConsent",
        components: {
            'v-snack': Snackbar
        },
        data: () => ({
            valid: false,
            overlay: false,
            overlayMessage: '',
            text: "",
            checkbox: false,
            firstname: '',
            lastname: '',
            nameRules: [
              v => !!v || 'Name is required',
              v => v.length <= 10 || 'Name must be less than 10 characters',
            ],
            email: '',
            emailRules: [
              v => !!v || 'E-mail is required',
              v => /.+@.+/.test(v) || 'E-mail must be valid',
            ],
        }),
        mounted() {
            // console.log('mounted');
            this.$vuetify.goTo(0);
            // console.log(localStorage.media_bias_lexica_annotator_survey_record_id_key)
        },
        methods: {
            submit() {
                this.overlay = true;
                this.overlayMessage = 'Submitting response ... ';
                axios
                    .post(`${BASE_API_URL}/survey_record/update_data_consent/`, {
                        body: {
                            survey_record_id: localStorage.media_bias_lexica_annotator_survey_record_id_key.toString(),
                            data_consent: this.checkbox
                        },
                        headers: {
                            'Content-Type': 'application/json'
                        }
                    })
                    .then((response) => this.handleResponse(response))
                    .catch((error) => {
                        this.$refs.snack.showSnackbar('error', 'Got nothing from server. Please check your internet connection or refresh the page.');
                        this.overlay = false;
                    });
            },
            handleResponse: function (response) {
                this.overlay = false;
                localStorage.media_bias_lexica_annotator_survey_record_id_key = response.data.survey_record_id;
                this.$router.replace({name: 'DemographicQuestions'});
            }
      }
    }
</script>

<style scoped>

</style>



