<template>
  <v-container>
    <v-app-bar fixed>
      <v-col class="text-left">
        <h4 class="text-justify font-weight-bold">Important info</h4>
      </v-col>
      <v-chip color="info" v-if="(breakpoint==='md'||breakpoint==='lg'||breakpoint==='xl')">
        <h4>Part {{carouselCounter + 1}} of {{8}}</h4>
      </v-chip>
      <v-chip color="info" v-if="(breakpoint==='xs'||breakpoint==='sm')">
        <h4>P. {{carouselCounter + 1}} of {{8}}</h4>
      </v-chip>
      <v-col class="text-right">
        <v-btn
          depressed
          v-if="(breakpoint==='xs'||breakpoint==='sm')"
          :disabled="!valid"
          color="success"
          value="right"
          v-on:click="submit"
        >
          <v-icon small dark>mdi-check-bold</v-icon> Submit
        </v-btn>
      </v-col>
    </v-app-bar>
    <v-row align="center" justify="center" class="mt-16">
      <v-carousel light :show-arrows="false" :cycle="false" :continuous="false" hide-delimiters hide-delimiter-background v-model="carouselCounter" :style="style">
        <v-carousel-item>
          <v-row justify="center" align="center">
            <v-card class="ma-md-4 pa-md-4 ma-4 pa-4" scrollable style="width: 500px;" outlined tile>
              <h4 class="text-justify font-weight-regular">
                In this survey, we <strong>attempt to annotate media bias instances</strong>.
                There exists a variety of media bias forms.
                We focus only on the bias that arises when journalists label the same concepts differently and choose different words to refer to the same concept.
              </h4>
              <br>
              <h4 class="text-justify font-weight-regular">
                In particular, we are looking for <strong>words that convey strong opinions/emotions</strong> and thus make a sentence prone to bias in the given context. Please note that bias is different from negative sentiment - <strong>bias is ambiguous and subtle</strong>, it can be positive, negative or not even have a particular sentiment but it still can imply or intensify the opinion/emotion.
              </h4>
            </v-card>
          </v-row>
        </v-carousel-item>
        <v-carousel-item>
          <v-row justify="center" align="center">
            <v-card class="ma-md-4 pa-md-4 ma-4 pa-4" scrollable style="width: 500px;" outlined tile>
              <h4 class="text-justify font-weight-regular">
                The following examples should help you to understand possible media bias instances better but they are not exhaustive. For each example, a sentence with a biased word (<strong class="blue--text">blue colored</strong>) is shown first followed by its impartial representation (<strong class="green--text">green colored</strong>). Obviously, for the annotation task itself, the colors are not provided.
              </h4>
            </v-card>
          </v-row>
        </v-carousel-item>
        <v-carousel-item>
          <v-row justify="center" align="center">
            <v-card class="ma-md-4 pa-md-4 ma-4 pa-4" scrollable style="width: 500px;" outlined tile>
              <h2 class="text-center font-weight-bold">Subjective Intensifiers</h2>
              <br>
              <div class="text-center">
                Schnabel himself did the <strong class="blue--text"> fantastic </strong> reproductions of Basquiats work.
              </div>
              <br>
              <div class="text-center">
                Schnabel himself did the <strong class="green--text"> accurate </strong> reproductions of Basquiats work.
              </div>
              <br>
            </v-card>
          </v-row>
        </v-carousel-item>
        <v-carousel-item>
          <v-row justify="center" align="center">
            <v-card class="ma-md-4 pa-md-4 ma-4 pa-4" scrollable style="width: 600px;" outlined tile>
              <h2 class="text-center font-weight-bold">Strong labels</h2>
              <br>
              <div class="text-center">
                'The people want the Truth!': Trump <strong class="blue--text"> gloats </strong> over the loss of American media jobs.
              </div>
              <br>
              <div class="text-center">
                'The people want the Truth!': Trump <strong class="green--text"> tweets </strong> over the loss of American media jobs.
              </div>
              <br>
            </v-card>
          </v-row>
        </v-carousel-item>
        <v-carousel-item>
          <v-row justify="center" align="center">
            <v-card class="ma-md-4 pa-md-4 ma-4 pa-4" scrollable style="width: 600px;" outlined tile>
              <h2 class="text-center font-weight-bold">One sided terms</h2>
              <br>
              <div class="text-center">
                Concerned Women for Americas major areas of political activity have consisted of opposition to gay causes, <strong class="blue--text"> pro-life </strong> law...
              </div>
              <br>
              <div class="text-center">
                Concerned Women for Americas major areas of political activity have consisted of opposition to gay causes, <strong class="green--text"> anti-abortion </strong> law...
              </div>
              <br>
            </v-card>
          </v-row>
        </v-carousel-item>
        <v-carousel-item>
          <v-row justify="center" align="center">
            <v-card class="ma-md-4 pa-md-4 ma-4 pa-4" scrollable style="width: 600px;" outlined tile>
              <h4 class="text-justify font-weight-bold">
                During the survey, you will be shown both biased and impartial sentences. Each sentence is associated with 3 following questions:
              </h4>
              <br>
              <p class="text-justify font-weight-regular">
                <v-icon small light>mdi-check-bold</v-icon> We would like you to highlight the words that convey strong opinion and are most likely to cause bias. You also can highlight short phrases, e.g., New York.
              </p>
              <p class="text-justify font-weight-regular">
                <v-icon small light>mdi-check-bold</v-icon> Label the sentence as biased or non-biased.
              </p>
              <p class="text-justify font-weight-regular">
                <v-icon small light>mdi-check-bold</v-icon> Label the sentence as factual or opinionated.
              </p>
            </v-card>
          </v-row>
        </v-carousel-item>
       <v-carousel-item>
          <v-row justify="center" align="center" >
            <v-card class="ma-md-4 pa-md-4 ma-4 pa-4" scrollable style="width: 700px;" outlined tile>
              <h4 class="text-justify font-weight-black">Before we continue, it is <strong class="green--text">important</strong> for us that you carefully read the statements below.</h4>
              <br>
              <h4 class="text-justify font-weight-regular">
                <v-icon small light>mdi-check-bold</v-icon> In annotating these articles, we ask you to <strong>leave aside your personal views</strong> of the topic of the statement as much as possible and focus instead only on how the statement as such was formulated.
              </h4>
              <br>
              <h4 class="text-justify font-weight-regular">
                <v-icon small light>mdi-check-bold</v-icon> Please note that even though a word can define a controversial topic or be connected to it, it <strong>doesn't always mean that the word itself is biased</strong>, for example, "abortion" or "migration".
              </h4>
              <br>
              <h4 class="text-justify font-weight-regular">
                <v-icon small light>mdi-check-bold</v-icon> Please select multiple words together only when they <strong>keep the meaning</strong> that they have in that particular context, <strong>only</strong> when they are used together. Otherwise, please select the words separately.
              </h4>
            </v-card>
          </v-row>
        </v-carousel-item>
        <v-carousel-item>
          <v-row justify="center" align="center" >
            <v-card class="ma-md-2 pa-md-2 ma-2 pa-2" scrollable style="width: 600px;" outlined tile>
              <v-form v-model="valid" ref="form">
                <v-col cols="12" v-for="(question, i) in questions" :key="`${i}-${question.id}`">
                  <h5 class="text-justify grey--text text--darken-1">Question</h5>
                  <h2 class="text-justify">{{ question.text }}</h2>
                  <h5 class="text-justify">Based on the information that was provided to you earlier, please select the correct option.</h5>
                  <v-radio-group
                      v-model="radioGroup.selection[question.name]"
                      :rules="radioRules"
                      v-if="question.type==='radio'"
                    >
                      <v-radio
                        color="success"
                        v-for="choice in question.simple_choices"
                        :key="`choice-${choice.id}`"
                        :label="choice.text"
                        :value="choice.id"
                      ></v-radio>
                    </v-radio-group>
                </v-col>
              </v-form>
            </v-card>
          </v-row>
        </v-carousel-item>

      </v-carousel>

    </v-row>
    <v-row class="mt-0" align="center" justify="center">
          <v-btn depressed style="" color="info" v-on:click="carouselCounter--" :disabled="carouselCounter===0">
            <v-icon small dark>mdi-arrow-left-thick</v-icon> previous
          </v-btn>
          <v-btn depressed color="warning" style="margin-left: 20px" v-on:click="carouselCounter++" :disabled="carouselCounter===8-1">
            next <v-icon small dark>mdi-arrow-right-thick</v-icon>
          </v-btn>
          <v-btn
              depressed
              v-if="(breakpoint==='md'||breakpoint==='lg'||breakpoint==='xl')"
              :disabled="!valid"
              style="margin-left: 20px"
              color="success"
              v-on:click="submit"
            >
              <v-icon small dark>mdi-check-bold</v-icon> <v-spacer></v-spacer> Submit
            </v-btn>
        </v-row>
    <v-overlay :value="overlay">
      <v-row style="flex-direction: column" justify="center" align="center">
        <v-progress-circular indeterminate size="48"></v-progress-circular>
        {{ overlayMessage }}
      </v-row>
    </v-overlay>
    <v-snack ref="snack"></v-snack>
    <v-dialog ref="dialog"></v-dialog>

  </v-container>
</template>

<script>
  import Snackbar from "../UI/Snackbar";
  import Dialog from "../UI/Dialog";
  export default {
      name: "IQC",
      components: {
          'v-snack': Snackbar,
          'v-dialog': Dialog
      },
      data: () => ({
          questions: [],
          carouselCounter: 0,
          radioGroup:{
              selection: [],
          },
          checkBoxGroup:{
              selection: [],
          },
          valid: false,
          overlay: false,
          overlayMessage: '',
          breakpoint: '',
          radioRules: [
            v => !!v || 'It is required'
          ],
      }),
      beforeMount() {
          // console.log(localStorage.media_bias_lexica_annotator_survey_record_id_key);
          // console.log(this.$vuetify.breakpoint.name);
          this.overlay = true;
          this.overlayMessage = 'Getting data ... ';
          this.$store.dispatch('loadSurveyFromID', 3).then(
              response => {
                  this.overlay = false;
                  this.questions = this.parseQuestions(response.survey.questions);
              },
              error => {
                  this.overlay = false;
                  this.$refs.snack.showSnackbar('error', 'Got nothing from server. Please check your internet connection or refresh the page.');
                  // console.log(error);
              }
          );
          this.breakpoint = this.$vuetify.breakpoint.name;
      },
      computed: {
          // survey() {
          //     return this.$store.getters.getSurvey
          // },
          style() {
              switch (this.$vuetify.breakpoint.name) {
                case 'xs': return 'height: 550px';
                case 'sm': return 'height: 550px';
                case 'md': return 'height: 400px';
                case 'lg': return 'height: 400px';
                case 'xl': return 'height: 400px';
              }
          }
      },
      watch: {
          // survey(newValue) {
          //     if (typeof newValue !== "undefined") {
          //         this.questions = this.parseQuestions(newValue.questions);
          //         console.log(this.questions);
          //     }
          // }
      },
      methods: {
          validate: function () {
              return this.$refs.form.validate()
          },
          parseQuestions: function(questions) {
              // console.log(questions);
              for (let i = 0; i < questions.length; i++) {
                  let choices = questions[i].simple_choices;
                  questions[i].simple_choices = _.orderBy(choices, 'id')
              }
              return questions;
          },
          submit: function() {
              // console.log(this.radioGroup);
              // console.log(this.radioGroup.selection['bias_sentiment']);
              this.overlay = true;
              this.overlayMessage = 'Submitting your response ... ';
              this.$store.dispatch('update_iqc_info', {
                  survey_record_id: localStorage.media_bias_lexica_annotator_survey_record_id_key,
                  quality_check_choice: this.radioGroup.selection['bias_sentiment']
              }).then(response => {
                  this.overlay = false;
                  this.handleResponse(response);
              }, error => {
                  this.overlay = false;
                  this.$refs.form.reset();
                  this.$refs.snack.showSnackbar('error', error.data);
              })
          },
          handleResponse: function (response) {
              // console.log(response.data.passed);
              // localStorage.media_bias_lexica_annotator_survey_record_id_key = response.data.survey_record_id;
              if (response.data.passed===true) {
                  this.$router.replace({name: 'Annotation'});
              } else {
                  this.carouselCounter = 0;
                  this.$refs.form.reset();
                  this.$refs.dialog.showErrorDialog("Incorrect choice", "This is not the correct option. It is important for the results that you understand the basic principles associated with bias. Please carefully read the information we have provided on this page and select the correct choice.");
              }
          },
    }
  }
</script>

<style scoped>

</style>



