<template>
    <v-container fluid fill-height>
    <v-row align="center" justify="center" style="flex-direction: column">
      <v-card  class="pa-12">
        <img src="../../assets/error.png"/>
        <br>
        <h2 style="margin-top: 20px" class="font-weight-bold">{{ msg }}</h2>
        <h4 class="font-weight-regular"> {{ details }}</h4>
        <br>
      </v-card>
      <v-btn style="margin-top: 48px" color="success" v-on:click="close">
        <v-icon small dark>mdi-home</v-icon> <v-spacer></v-spacer> home
      </v-btn>
    </v-row>
  </v-container>
</template>

<script>
    export default {
        name: "ErrorPage",
        props: ['msg', 'details'],
        mounted() {

        },
        methods: {
            close: function () {
                this.$router.push({name: 'Welcome'});
            }
        }
    }
</script>

<style scoped>

</style>



