<template>
  <div>
    <h5 class="text-justify">Please fill in the correct answer.</h5>
    <v-text-field
      :label="question.name.toUpperCase()"
      outlined
      class="mt-6"
      color="info"
      :type="type"
      v-model="txtField"
      v-on:input="recordResponse()"
    ></v-text-field>
  </div>

</template>

<script>
    export default {
        name: "TextField",
        data() {
            return {
                txtField: '',
            }
        },
        props: {
            question: Object,
            type: String
        },
        methods: {
            recordResponse: function () {
                this.$emit('recordTextResponse', this.question, this.txtField);
            }
        }
    }
</script>

<style scoped>

</style>



