<template >
  <v-container fluid fill-height>
    <v-row align="center" justify="center">
      <v-card  class="pa-12" max-width="600px" outlined tile>
        <img class="ma-4" src="../../assets/survey.png" style="width: 20%"/>
        <h2 style="margin-top: 20px" class="font-weight-black">Welcome</h2>
        <br>
        <h4 class="text-justify font-weight-regular">
          We welcome you to participate in <strong class="font-weight-black">media bias survey and annotation</strong>.
          The study takes most people almost <strong class="font-weight-black">15 - 20 minutes</strong> to complete.
          <br>
        </h4>
        <br>
        <h4 class="text-justify font-weight-regular">
          Please enter you <strong class="font-weight-black">Amazon MTurk</strong> user name in the box below to proceed.
        </h4>
        <br>
        <h4 class="text-justify font-weight-bold">
          All the questions, choices and the numbers shown in the survey are for illustration purposes only.
        </h4>
        <br>
        <v-form v-model="valid" ref="form" v-on:submit.prevent="submit">
          <v-text-field
            v-model="identifierKey"
            label="Placeholder input field"
            outlined
            :rules="[v => !!v || 'You must fill this field to continue!']"
          ></v-text-field>
        </v-form>
        <v-btn
          depressed
          large
          color="success"
          dark
          v-on:click="submit"
          :disabled="!valid"
        >
          START
        </v-btn>
      </v-card>
    </v-row>
    <v-overlay :value="overlay">
      <v-row style="flex-direction: column" justify="center" align="center">
        <v-progress-circular indeterminate size="48"></v-progress-circular>
        {{ overlayMessage }}
      </v-row>
    </v-overlay>
    <v-snack ref="snack"></v-snack>
    <v-dialog ref="dialog"></v-dialog>
  </v-container>

</template>

<script>
  import axios from 'axios';
  import Snackbar from "../UI/Snackbar";
  import Dialog from "../UI/Dialog";
  const BASE_API_URL = process.env.API_URL;
  export default {
      name: "Welcome",
      components: {
          'v-snack': Snackbar,
          'v-dialog': Dialog
      },
      data () {
        return {
            msg: "",
            overlay: false,
            overlayMessage: '',
            identifierKey: '',
            questions: [],
            valid: false,
            radioRules: [
              v => !!v || 'It is required'
            ],
        }
      },
      mounted() {
          this.$vuetify.goTo(0);
      },
      beforeRouteEnter(to, from, next) {
        if (to.query.redirectFrom) {
            next(vm => {
              vm.$refs.dialog.showErrorDialog('Forbidden', to.query.message)
            });
        } else {
          next()
        }
      },
      methods: {
          validate: function () {
              return this.$refs.form.validate()
          },
          submit: function() {
              if (this.validate()) {
                  const isSurveyCompleted = localStorage.media_bias_lexica_annotator_survey_completed_by_user;
                  // console.log(isSurveyCompleted);
                  if (localStorage.media_bias_lexica_annotator_timeout === 'true') {
                      this.$store.dispatch('set_user_identified', false);
                      this.$refs.dialog.showErrorDialog('Your session was timed out earlier. You can no longer participate in the survey. The survey has been closed now.')
                  }
                  else if (typeof isSurveyCompleted === "undefined" || isSurveyCompleted === 'false') {
                      this.overlay = true;
                      this.overlayMessage = 'Submitting response ... ';
                      axios
                          .post(`${BASE_API_URL}/survey_record/`, {
                              body: {
                                  identifier_key: this.identifierKey
                              },
                              headers: {
                                  'Content-Type': 'application/json'
                              }
                          })
                          .then((response) => this.handleResponse(response))
                          .catch((error) => {
                              this.$store.dispatch('set_user_identified', false);
                              this.$refs.snack.showSnackbar('error', 'Got nothing from server. Please check your internet connection or refresh the page.');
                              this.overlay = false;
                          });
                  } else {
                      this.$store.dispatch('set_user_identified', false);
                      this.$refs.dialog.showSuccessDialog('Survey already completed', 'You can go ahead and close the tab. We thank you again for taking part in this survey.')
                  }
              }
          },
          handleResponse: function (response) {
              this.$store.dispatch('set_user_identified', true);
              localStorage.media_bias_lexica_annotator_survey_record_id_key = response.data;
              this.overlay = false;
              this.$router.replace({name: 'DataConsent'});
          }
      }
    }
</script>

<style scoped>
</style>



