import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import _ from 'lodash'

const BASE_API_URL = process.env.API_URL;
Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    surveys: [],
    survey: [],
    surveySentences: [],
    authUserIdentified: false,
    surveyCompletedByUser: false,
  },
  actions: {
    set_user_identified({ commit, state }, flag) {
       commit('SET_USER_IDENTIFIED', flag);
    },
    set_survey_completed_by_user({ commit, state }, flag) {
       commit('SET_SURVEY_COMPLETED_BY_USER', flag);
    },
    // asynchronous operations
    removeUserFromPool({ commit, state }, payload) {
      return new Promise((resolve, reject) => {
        axios
          .post(`${BASE_API_URL}/timeout/`, {
            body: {
              survey_record_id: payload.survey_record_id,
            },
            headers: {
                'Content-Type': 'application/json'
            }
          })
          .then((response) => {
            resolve(response.data);
          })
          .catch((error) => reject(error));
      });
    },
    loadSurveyFromID({ commit, state }, surveyId) {
      return new Promise((resolve, reject) => {
        axios
          .get( `${BASE_API_URL}/surveys/${surveyId}/`)
          .then((response) => {
            commit('SET_SURVEY', response.data);
            resolve(response.data);
          })
          .catch((error) => reject(error));
      });
    },
    loadRandomSurveySentences({ commit, state }) {
      return new Promise((resolve, reject) => {
        axios
          .get( `${BASE_API_URL}/survey_sentences/`)
          .then((response) => {
            commit('SET_SURVEY_SENTENCES', response.data);
            resolve(response.data);
          })
          .catch((error) => reject(error));
      });
    },
    update_demographic_info({ commit, state }, payload) {
      // console.log(payload);
      return new Promise((resolve, reject) => {
        axios
          .post(`${BASE_API_URL}/survey_record/update_demographic_info/`, {
            body: {
              survey_record_id: payload.survey_record_id,
              age: payload.age,
              gender: payload.gender,
              education: payload.education,
              native_english_speaker: payload.native_english_speaker
            },
            headers: {
                'Content-Type': 'application/json'
            }
          })
          .then((response) => resolve(response))
          .catch((error) => reject(error));
      });
    },
    update_ideology_info({ commit, state }, payload) {
      // console.log(payload);
      return new Promise((resolve, reject) => {
        axios
          .post(`${BASE_API_URL}/survey_record/update_ideology_info/`, {
            body: {
              survey_record_id: payload.survey_record_id,
              political_ideology: payload.political_ideology,
              news_check_frequency: payload.news_check_frequency,
              followed_news_outlets: payload.followed_news_outlets,
            },
            headers: {
                'Content-Type': 'application/json'
            }
          })
          .then((response) => resolve(response))
          .catch((error) => reject(error));
      });
    },
    update_iqc_info({ commit, state }, payload) {
      // console.log(payload);
      return new Promise((resolve, reject) => {
        axios
          .post(`${BASE_API_URL}/survey_record/quality_control/`, {
            body: {
                survey_record_id: payload.survey_record_id,
                quality_check_choice: payload.quality_check_choice
            },
            headers: {
                'Content-Type': 'application/json'
            }
          })
          .then((response) => resolve(response))
          .catch((error) => reject(error));
      });
    },
    annotate_sentence({ commit, state }, payload) {
      // console.log(payload);
      return new Promise((resolve, reject) => {
        axios
          .post(`${BASE_API_URL}/annotate_sentence/`, {
            body: {
              survey_record_id: payload.survey_record_id,
              survey_sentence_id: payload.survey_sentence_id,
              sentence_group_id: payload.group_id,
              label: payload.label,
              words: payload.words,
              factual: payload.factual
            },
            headers: {
                'Content-Type': 'application/json'
            }
          })
          .then((response) => resolve(response))
          .catch((error) => reject(error));
      });
    },
  },
  mutations: {
    SET_SURVEY(state, payload) {
      state.survey = payload.survey;
    },
    SET_SURVEY_SENTENCES(state, payload) {
      state.surveySentences = payload.survey_sentences;
    },
    SET_USER_IDENTIFIED(state, payload) {
      state.authUserIdentified = payload;
      localStorage.media_bias_lexica_annotator_user_is_identified = payload;
    },
    SET_SURVEY_COMPLETED_BY_USER(state, payload) {
      state.surveyCompletedByUser = payload;
      localStorage.media_bias_lexica_annotator_survey_completed_by_user = payload;
      localStorage.media_bias_lexica_annotator_user_is_identified = !payload;
    },
  },
  getters: {
    getSurvey: state => state.survey,
    getSurveySentences: state => state.surveySentences,
    getAuthUserIdentified: state => state.authUserIdentified,
    isSurveyCompletedByUser: state => state.surveyCompletedByUser
  }
});

export default store



