<template>
    <v-container>
      <v-snackbar
        v-model="snackbar"
        :color="snackbarType"
        timeout=4000
        vertical
        top
      >
        <h3 class="font-weight-bold">
          {{ snackbarText}}
        </h3>

        <template v-slot:action="{ attrs }">
          <v-btn
            color="white"
            text
            v-bind="attrs"
            @click="snackbar = false"
          >
            Hide
          </v-btn>
        </template>
      </v-snackbar>
    </v-container>
</template>

<script>
    export default {
        name: "Snackbar",
        data() {
            return {
                snackbar: false,
                snackbarType: '',
                snackbarText: ''
            }
        },
        methods: {
            showSnackbar(snackbarType, snackbarText) {
                this.snackbar = true;
                this.snackbarType = snackbarType;
                this.snackbarText = snackbarText;
            }
        }
    }
</script>

<style scoped>

</style>



