<template>
    <v-dialog v-model="infoDialog" fullscreen>
      <v-card outlined tile style="width: 700px;">
        <v-row align="center" justify="center" style="flex-direction: column">
          <v-carousel light :show-arrows="false" :cycle="false" :continuous="false" hide-delimiters hide-delimiter-background v-model="carouselCounter">
            <v-carousel-item>
              <v-row justify="center" align="center" class="mt-8">
                <v-card class="ma-md-4 pa-md-4 ma-4 pa-4" style="width: 700px;" outlined tile>
                  <h4 class="text-justify font-weight-regular">
                    In this survey, we <strong>attempt to annotate media bias instances</strong>.
                    There exists a variety of media bias forms.
                    We focus only on the bias that arises when journalists label the same concepts differently and choose different words to refer to the same concept.
                  </h4>
                  <br>
                  <h4 class="text-justify font-weight-regular">
                    In particular, we are looking for <strong>words that convey strong opinions/emotions</strong> and thus make a sentence prone to bias in the given context. Please note that bias is different from negative sentiment - <strong>bias is ambiguous and subtle</strong>, it can be positive, negative or not even have a particular sentiment but it still can imply or intensify the opinion/emotion.
                  </h4>
                </v-card>
              </v-row>
            </v-carousel-item>
            <v-carousel-item>
              <v-row justify="center" align="center" class="mt-8">
                <v-card class="ma-md-4 pa-md-4 ma-4 pa-4" scrollable style="width: 500px;" outlined tile>
                  <h4 class="text-justify font-weight-regular">
                    The following examples should help you to understand possible media bias instances better but they are not exhaustive. For each example, a sentence with a biased word (<strong class="blue--text">blue colored</strong>) is shown first followed by its impartial representation (<strong class="green--text">green colored</strong>). Obviously, for the annotation task itself, the colors are not provided.
                  </h4>
                </v-card>
              </v-row>
            </v-carousel-item>
            <v-carousel-item>
              <v-row justify="center" align="center" class="mt-8">
                <v-card class="ma-md-4 pa-md-4 ma-4 pa-4" scrollable style="width: 500px;" outlined tile>
                  <h2 class="text-center font-weight-bold">Subjective Intensifiers</h2>
                  <br>
                  <div class="text-center">
                    Schnabel himself did the <strong class="blue--text"> fantastic </strong> reproductions of Basquiats work.
                  </div>
                  <br>
                  <div class="text-center">
                    Schnabel himself did the <strong class="green--text"> accurate </strong> reproductions of Basquiats work.
                  </div>
                  <br>
                </v-card>
              </v-row>
            </v-carousel-item>
            <v-carousel-item>
              <v-row justify="center" align="center" class="mt-8">
                <v-card class="ma-md-4 pa-md-4 ma-4 pa-4" scrollable style="width: 600px;" outlined tile>
                  <h2 class="text-center font-weight-bold">Strong labels</h2>
                  <br>
                  <div class="text-center">
                    'The people want the Truth!': Trump <strong class="blue--text"> gloats </strong> over the loss of American media jobs.
                  </div>
                  <br>
                  <div class="text-center">
                    'The people want the Truth!': Trump <strong class="green--text"> tweets </strong> over the loss of American media jobs.
                  </div>
                  <br>
                </v-card>
              </v-row>
            </v-carousel-item>
            <v-carousel-item>
              <v-row justify="center" align="center" class="mt-8">
                <v-card class="ma-md-4 pa-md-4 ma-4 pa-4" scrollable style="width: 600px;" outlined tile>
                  <h2 class="text-center font-weight-bold">One sided terms</h2>
                  <br>
                  <div class="text-center">
                    Concerned Women for Americas major areas of political activity have consisted of opposition to gay causes, <strong class="blue--text"> pro-life </strong> law...
                  </div>
                  <br>
                  <div class="text-center">
                    Concerned Women for Americas major areas of political activity have consisted of opposition to gay causes, <strong class="green--text"> anti-abortion </strong> law...
                  </div>
                  <br>
                </v-card>
              </v-row>
            </v-carousel-item>
            <v-carousel-item>
              <v-row justify="center" align="center" class="mt-8">
                <v-card class="ma-md-4 pa-md-4 ma-4 pa-4" scrollable style="width: 600px;" outlined tile>
                  <h4 class="text-justify font-weight-bold">
                    During the survey, you will be shown both biased and impartial sentences. Each sentence is associated with 3 following questions:
                  </h4>
                  <br>
                  <p class="text-justify font-weight-regular">
                    1. We would like you to highlight the words that, from your point of view, convey strong opinion and are most likely to cause bias. You also can highlight short phrases, e.g., New York.
                  </p>
                  <p class="text-justify font-weight-regular">
                    2. Label the sentence as biased or non-biased.
                  </p>
                  <p class="text-justify font-weight-regular">
                    3. Label the sentence as factual or opinionated.
                  </p>
                </v-card>
              </v-row>
            </v-carousel-item>
            <v-carousel-item>
              <v-row justify="center" align="center" class="mt-8">
                <v-card class="ma-md-4 pa-md-4 ma-4 pa-4" scrollable style="width: 500px;" outlined tile>
                  <h3 class="text-justify font-weight-regular">Before we continue, it is <strong class="green--text">important</strong> for us that you carefully read the statements below.</h3>
                  <br>
                  <h4 class="text-justify font-weight-regular">
                    In annotating these articles, we ask you to <strong>leave aside your personal views</strong> of the topic of the statement as much as possible and focus instead only on how the statement as such was formulated.
                  </h4>
                  <br>
                  <h4 class="text-justify font-weight-regular">
                    Please note that even though a word can define a controversial topic or be connected to it, it <strong>doesn't always mean that the word itself is biased</strong>, for example, "abortion" or "migration".
                  </h4>
                </v-card>
              </v-row>
            </v-carousel-item>
            <v-row class="mt-n10" align="center" justify="center">
              <v-btn depressed style="" color="info" v-on:click="carouselCounter--" :disabled="carouselCounter===0">
                <v-icon small dark>mdi-arrow-left-thick</v-icon> previous
              </v-btn>
              <v-btn depressed color="success" style="margin-left: 20px" v-on:click="carouselCounter++" :disabled="carouselCounter===7-1">
                next <v-icon small dark>mdi-arrow-right-thick</v-icon>
              </v-btn>
              <v-btn depressed color="warning" style="margin-left: 20px" v-on:click="infoDialog=false">
                close <v-icon small dark>mdi-close-thick</v-icon>
              </v-btn>
            </v-row>
          </v-carousel>
        </v-row>
      </v-card>
    </v-dialog>
</template>

<script>
    export default {
        name: "InstructionsModal",
        data() {
            return {
                infoDialog: false,
                carouselCounter: 0
            }
        },
        mounted() {

        },
        methods: {
            show() {
                this.carouselCounter = 0;
                this.infoDialog = true;
            },
        }
    }
</script>

<style scoped>

</style>



