<template>
  <v-container>
    <v-dialog v-model="dialogError" persistent max-width="400">
      <v-card class="pa-4" outlined tile>
        <img class="ma-4" src="../../assets/error.png" style="width: 20%"/>
        <h3 class="text-center my-4 red--text">{{ header }}</h3>
        <h4 class="text-justify px-6 font-weight-regular text--darken-3 grey--text">{{ message }}</h4>
        <br>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn depressed color="warning" @click="dialogError = false">okay</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialogSuccess" persistent max-width="400">
      <v-card class="pa-4" outlined tile>
        <img class="ma-4" src="../../assets/tick.png" style="width: 20%"/>
        <h3 class="text-center my-4 green--text">{{ header }}</h3>
        <h4 class="text-justify font-weight-bold px-6 text--darken-2 grey--text">{{ message }}</h4>
        <br>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn depressed color="success" @click="dialogSuccess = false">okay</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>

</template>

<script>
    export default {
        name: "Dialog",
        data() {
            return {
                dialogError: false,
                dialogSuccess: false,
                header: '',
                message: '',
            }
        },
        methods: {
            showErrorDialog(header, message) {
                this.dialogError = true;
                this.header = header;
                this.message = message;
            },
            showSuccessDialog(header, message) {
                this.dialogSuccess = true;
                this.header = header;
                this.message = message;
            },
        }
    }
</script>

<style scoped>

</style>



