<template>
  <v-container>
    <v-app-bar fixed>
      <v-col class="text-left">
        <h3 class="text-justify font-weight-bold">{{ pageLabel}}</h3>
      </v-col>
      <v-col class="text-right">
        <v-btn
          :disabled="!activate"
          color="success"
          value="right"
          v-on:click="submit"
        >
          Next <v-icon small dark>mdi-forward</v-icon> <v-spacer></v-spacer>
        </v-btn>
      </v-col>
    </v-app-bar>
    <v-row align="center" justify="center">
      <v-carousel light :show-arrows="false" :cycle="false" :continuous="false" hide-delimiter-background v-model="cId" :style="style">
        <v-form ref="form" v-model="valid" v-on:submit.prevent="submit">
          <v-carousel-item
            v-for="(question, i) in questions"
            :key="`${i}-${question.id}`"
          >
            <v-row justify="center" align="center">
              <v-card class="ma-md-4 pa-md-4 ma-4 pa-4" scrollable style="width: 700px;" outlined tile>
                <h5 class="text-justify">Question</h5>
                <h3 class="text-justify">{{ question.text }}</h3>
                <h5 class="text-justify" v-if="question.type==='radio'">Please select one option.</h5>
                <h5 class="text-justify" v-if="question.type==='checkbox'">You can select more than one option.</h5>
                <h5 class="text-justify" v-if="question.type==='highlight'">
                  Use your mouse pointer/finger to select the text. Once you are done selecting, your selection will be automatically displayed below.
                  <strong class="green--text">It is possible to highlight both words and short phrases, such, illegal aliens.</strong>
                </h5>
                <div style="overflow-y: scroll;" v-else-if="question.type==='checkbox'">

                </div>
              </v-card>
            </v-row>
          </v-carousel-item>
        </v-form>
      </v-carousel>
    </v-row>
  </v-container>
</template>

<script>
    export default {
        name: "SliderQuestions",
        data() {
            return {

                countChips: '',


                cId: '',
                chipGroup:{
                    chips: [],
                },
            }
        },
        props: {
            questions: Array,
            pageLabel: String
        },
        mounted() {
            this.$vuetify.goTo(0);
        },
        computed: {
            style() {
              switch (this.$vuetify.breakpoint.name) {
                case 'xs': return 'height: 550px';
                case 'sm': return 'height: 550px';
                case 'md': return 'height: 500px';
                case 'lg': return 'height: 500px';
                case 'xl': return 'height: 500px';
              }
            }
        },
        watch: {

        },
        methods: {

            validate: function () {
                return this.$refs.form.validate()
            },

            submit: function () {
                if (this.validate()) {
                    console.log(this.customNewsOutlet);
                    const data = {
                        'radioGroup': this.radioGroup,
                        'rangeSlider': this.slider,
                        'checkboxGroup': this.checkBoxGroup,
                        'customNewsOutlet': this.customNewsOutlet
                    };
                    this.$parent.submit(data);
                }
            }
        }
    }
</script>

<style scoped>

</style>



