<template>
  <div>
    <h5 class="text-justify">Please select one option.</h5>
    <v-radio-group
      v-model="selection[question.name]"
      :rules="radioRules"
    >
      <v-radio
        v-for="choice in question.simple_choices"
        :key="`choice-${choice.id}`"
        :label="choice.text"
        :value="choice.id"
        color="success"
        v-on:change="recordResponse(choice)"
      ></v-radio>
    </v-radio-group>
  </div>
</template>

<script>
    export default {
        name: "Radio",
        data() {
            return {
                selection: [],
                hintHidden: false,
                radioRules: [
                  v => !!v || 'It is required'
                ],
            }
        },
        mounted() {

        },
        props: {
            question: Object
        },
        methods: {
            recordResponse: function (choice) {
                console.log(this.selection);
                this.hintHidden = true;
                this.$emit('recordChoice', this.question, choice);
                // this.$parent.recordChoice(this.question, this.selection);
                // this.$parent.recordChoice(this.question, choice);
            }
        }
    }
</script>

<style scoped>

</style>



