<template>
    <div>
      <h5 class="text-justify">You can select more than one option.</h5>
      <v-row
        v-if="breakpoint==='xs' || breakpoint==='sm'"
        style="flex-direction: column"
        justify="start" align="center"
        class="ma-md-0 ma-0"
      >
        <br>
        <v-btn color="success" v-on:click="dialog=true" outlined rounded>
          choose from list
        </v-btn>
        <h4 class="blue-grey--text mt-2">{{ chipSelectionModel.length }} selected </h4>
        <v-dialog v-model="dialog" scrollable>
          <v-card>
            <v-card-title>Select outlets</v-card-title>
            <v-divider></v-divider>
            <v-card-text>
              <v-chip-group
                column
                multiple
              >
                <v-chip
                  v-for="choice in question.simple_choices"
                  v-model=" selection[choice.id]"
                  :key="`choice-${choice.id}`"
                  :value="choice.id"
                  color="success"
                  filter
                  outlined
                  style="margin: 8px"
                  v-on:click="recordResponse(choice)"
                >
                  {{ choice.text}}
                </v-chip>
              </v-chip-group>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="success" @click="dialog = false">Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-row>
      <v-row
        v-else-if="breakpoint==='lg' || breakpoint==='md' || breakpoint==='xl' || breakpoint==='sm'"
        style="flex-direction: column"
        justify="start" align="center"
        class="ma-md-0 ma-0"
      >
        <br>
        <v-chip-group
          column
          multiple
        >
          <v-chip
            v-for="choice in question.simple_choices"
            v-model="selection[choice.id]"
            :key="`choice-${choice.id}`"
            :value="choice.id"
            color="success"
            filter
            outlined
            v-on:click="recordResponse(choice)"
          >
            {{ choice.text }}
          </v-chip>
        </v-chip-group>
      </v-row>
      <br>
      <h5 class="text-justify mb-2">If you do not see the outlet of your choice, please mention it here.</h5>
      <v-row class="mx-0" style="flex-direction: column;" >
        <v-text-field
          class="mb-n4"
          v-model="customInput"
          placeholder="Favourite news outlet"
          outlined
          color="info"
          v-on:="recordResponse(customInput)"
        ></v-text-field>
        <v-btn
          color="info"
          :disabled="!customInput.length > 0"
          v-bind:color="(customInputAdded===true) ? 'success' : 'info'"
          v-on:click="recordCustomResponse"
        >
          <v-icon dense dark>{{customInputButtonIcon}}</v-icon>
          {{ customInputButtonLabel }}
        </v-btn>
      </v-row>
    </div>
</template>

<script>
    export default {
        name: "MultiChipSelect",
        data() {
            return {
                breakpoint: '',
                selection: [],
                dialog: false,
                customInput: '',
                customInputAdded: false,
                customInputButtonLabel: 'Add',
                customInputButtonIcon: 'mdi-plus'
            }
        },
        mounted() {
            this.breakpoint = this.$vuetify.breakpoint.name;
        },
        computed: {
            chipSelectionModel() {
                return this.selection.filter( value => {
                    return value === true;
                })
            }
        },
        props: {
            question: Object
        },
        methods: {
            recordResponse: function (choice) {
                console.log(this.selection);
                this.$emit('recordChoice', this.question, choice);
                // this.$parent.recordChoice(this.question, this.selection);
                // this.$parent.recordChoice(this.question, choice);
            },
            recordCustomResponse: function () {
                console.log(this.customInput);
                this.selection['customInput'] = this.customInput;
                this.chipSelectionModel.push(this.customInput);
                this.customInputAdded = true;
                this.customInputButtonLabel = 'Added';
                this.customInputButtonIcon = 'mdi-check';
                this.$emit('recordChoice', this.question, this.customInput);
                // this.$parent.recordChoice(this.question, this.selection);
                // this.$parent.recordChoice(this.question, choice);
            }
        }
    }
</script>

<style scoped>

</style>



