<template>
    <v-container fluid fill-height>
    <v-row align="center" justify="center" style="flex-direction: column">
      <v-card  class="pa-12">
        <img src="../../assets/tick.png" style="width: 20%;"/>
        <h2 class="font-weight-bold mt-4">Thank you!</h2>
        <h4 class="font-weight-regular">You have successfully completed the survey.</h4>
        <br>
        <h4 class="font-weight-bold">Please copy the code below and enter it in your Amazon MTurk portal</h4>
        <br>
        <h3 class="font-weight-bold green--text pa-6 pa-xs-0 pa-sm-2" style="border-style: solid; border-width: 2px">
          {{ id }}
        </h3>
      </v-card>
      <v-snackbar
        v-model="snackbar"
        color="success"
        timeout=2000
        vertical
      >
        Text copied!
      </v-snackbar>
    </v-row>
  </v-container>
</template>

<script>
    export default {
        name: "EndSurvey",
        data() {
            return {
                id: '',
                snackbar: false
            }
        },
        mounted() {
            this.id = localStorage.media_bias_lexica_annotator_survey_record_id_key;
            this.$store.dispatch('set_survey_completed_by_user', true);
        },
        methods: {
            copy: function (text) {
                let input=document.getElementById("idBtn");
                document.execCommand("copy");
                this.snackbar = true;
            },
            close: function () {
                this.$router.push({name: 'Welcome'});
            }
        }
    }
</script>

<style scoped>

</style>



