import Vue from 'vue'
import Vuetify from "vuetify"
import Router from 'vue-router'
import DataConsent from "../components/Pages/DataConsent";
import DemographicQ from "../components/Pages/DemographicQ";
import IdeologicalQ from "../components/Pages/IdeologicalQ";
import Welcome from "../components/Pages/Welcome";
import IQC from "../components/Pages/IQC";
import Annotation from "../components/Pages/Annotation";
import EndSurvey from "../components/Pages/EndSurvey";
import AdminDashboard from "../components/Pages/AdminDashboard";
import ErrorPage from "../components/Pages/ErrorPage";
import "vuetify/dist/vuetify.min.css";
import store from "../store";

Vue.use(Router);
Vue.use(Vuetify);

function guardRoute(to, from, next) {
  // next()
  if (to.meta.requiresAuth) {
    // let hasPermission = store.getters.getAuthUserIdentified;
    /* Vuex loses state on page refresh, hence local storage is a better option */
    let hasPermission = localStorage.media_bias_lexica_annotator_user_is_identified;
    let surveyCompleted = localStorage.media_bias_lexica_annotator_survey_completed_by_user;
    if (surveyCompleted === 'true') {
      next({
        name: "Welcome",
        query: {
          redirectFrom: to.fullPath,
          message: 'Survey already completed! You can go ahead and close the tab. We thank you again for taking part in this survey.',
        }
      })
    }
    else if (typeof hasPermission === "undefined" || hasPermission === 'false') {
      next({
        name: "Welcome",
        query: {
          redirectFrom: to.fullPath,
          message: 'Seems like you forgot to provide your identifier ID. Please fill out the required field and you will be taken to the survey immediately.',
        }
      })
    }
    else next()
  }
  else {
     next()
  }
}

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/admin/',
      name: 'AdminDashboard',
      component: AdminDashboard,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/data-consent/',
      name: 'DataConsent',
      component: DataConsent,
      beforeEnter: guardRoute,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/getting-started/',
      name: 'DemographicQuestions',
      component: DemographicQ,
      beforeEnter: guardRoute,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/almost-there/',
      name: 'IdeologicalQuestions',
      component: IdeologicalQ,
      beforeEnter: guardRoute,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/info/',
      name: 'IQC',
      component: IQC,
      beforeEnter: guardRoute,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/annotation/',
      name: 'Annotation',
      component: Annotation,
      beforeEnter: guardRoute,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/thank-you/',
      name: 'EndSurvey',
      component: EndSurvey,
      beforeEnter: guardRoute,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/',
      name: 'Welcome',
      component: Welcome
    },
  ]
});




