<template>
  <v-container>
    <v-app-bar fixed>
      <v-col class="text-left">
        <h4 class="text-justify font-weight-bold"> Getting started </h4>
      </v-col>
      <v-chip color="info" v-if="(breakpoint==='md'||breakpoint==='lg'||breakpoint==='xl')">
        <h4>Question {{cId + 1}} of {{questions.length}}</h4>
      </v-chip>
      <v-chip color="info" v-if="(breakpoint==='xs'||breakpoint==='sm')">
        <h4>Q. {{cId + 1}} of {{questions.length}}</h4>
      </v-chip>
      <v-col class="text-right" >
        <v-btn
          depressed
          v-if="(breakpoint==='xs'||breakpoint==='sm')"
          :disabled="!activate"
          color="success"
          value="right"
          v-on:click="submit"
        >
          <v-icon small dark>mdi-check-bold</v-icon> <v-spacer></v-spacer> Submit
        </v-btn>
      </v-col>
    </v-app-bar>
    <v-row align="center" justify="center" class="mt-16">
      <v-carousel light :show-arrows="false" :hide-delimiters="hideDelimiters" :cycle="false" :continuous="false" :hide-delimiter-background="true" v-model="cId">
        <v-form ref="form" v-model="valid">
          <v-carousel-item v-for="(question, i) in questions" :key="`${i}-${question.id}`">
            <v-row justify="center" align="center">
              <v-card class="ma-md-4 pa-md-4 ma-4 pa-4" scrollable style="width: 700px;" outlined tile>
                <h5 class="text-justify grey--text text--darken-2">Question</h5>
                <h3 class="text-justify">{{ question.text }}</h3>
                <v-rad ref="radio" v-if="question.type==='radio'" :question="question" @recordChoice="recordChoice"></v-rad>
                <v-txt-field type="number" ref="text_field" v-else-if="question.type==='text_field'" :question="question" @recordTextResponse="recordTextResponse"></v-txt-field>
                <v-row align="center" justify="center">
                  <v-btn depressed color="info" v-on:click="decrementSlide" :disabled="cId===0">
                    <v-icon small dark>mdi-arrow-left-thick</v-icon> previous
                  </v-btn>
                  <v-btn depressed color="warning" style="margin-left: 20px" v-on:click="incrementSlide" :disabled="cId===questions.length-1">
                    next <v-icon small dark>mdi-arrow-right-thick</v-icon>
                  </v-btn>
                  <v-btn
                    depressed
                    v-if="(breakpoint==='md'||breakpoint==='lg'||breakpoint==='xl')"
                    :disabled="!activate"
                    style="margin-left: 20px"
                    color="success"
                    v-on:click="submit"
                  >
                    <v-icon small dark>mdi-check-bold</v-icon> <v-spacer></v-spacer> Submit
                  </v-btn>
                </v-row>
              </v-card>
            </v-row>
          </v-carousel-item>
          <br>
        </v-form>
      </v-carousel>
    </v-row>
    <v-overlay :value="overlay">
      <v-row style="flex-direction: column" justify="center" align="center">
        <v-progress-circular indeterminate size="48"></v-progress-circular>
        {{ overlayMessage }}
      </v-row>
    </v-overlay>
    <v-snack ref="snack"></v-snack>
  </v-container>
</template>

<script>
  import _ from 'lodash'
  import Snackbar from "../UI/Snackbar";
  import Radio from "../ResponseTypes/Radio";
  import MultiChipSelect from "../ResponseTypes/MultiChipSelect";
  import TextField from "../ResponseTypes/TextField";
  export default {
      name: "DemographicQ",
      components: {
          'v-snack': Snackbar,
          'v-rad': Radio,
          'v-multi-chip': MultiChipSelect,
          'v-txt-field': TextField
      },
      data () {
          return {
              cId: 0,
              record: [],
              breakpoint: '',
              selected: '',
              activate: false,
              overlayMessage: '',
              completedQuestions: [],
              responses: {},
              overlay: false,
              msg: "",
              questions: [],
              valid: false,
          }
      },
      beforeMount() {
          this.overlay = true;
          this.overlayMessage = 'Getting data ... ';
          this.$store.dispatch('loadSurveyFromID', 1).then(
              response => {
                  this.overlay = false;
                  this.questions = this.parseQuestions(response.survey.questions);
              },
              error => {
                  this.overlay = false;
                  this.$refs.snack.showSnackbar('error', 'Got nothing from server. Please check your internet connection or refresh the page.');
              }
          );
          this.breakpoint = this.$vuetify.breakpoint.name;
          this.$vuetify.goTo(0);
      },
      computed: {
          style() {
              switch (this.$vuetify.breakpoint.name) {
                case 'xs': return 'height: 750px';
                case 'sm': return 'height: 750px';
                case 'md': return 'height: 600px';
                case 'lg': return 'height: 600px';
                case 'xl': return 'height: 600px';
              }
          },
          hideDelimiters() {
              switch (this.$vuetify.breakpoint.name) {
                case 'xs': return true;
                case 'sm': return true;
                case 'md': return true;
                case 'lg': return true;
                case 'xl': return true;
              }
          }
      },
      watch: {
          completedQuestions(newValue) {
              if (newValue.length === this.questions.length) {
                  this.activate = true;
              }
          }
      },
      methods: {
          validate: function () {
              return this.$refs.form.validate()
          },
          parseQuestions: function(questions) {
              for (let i = 0; i < questions.length; i++) {
                  let choices = questions[i].simple_choices;
                  questions[i].simple_choices = _.orderBy(choices, 'id')
              }
              return questions;
          },
          submit: function() {
              this.overlay = true;
              this.overlayMessage = 'Submitting your response ... ';
              this.$store.dispatch('update_demographic_info', {
                  survey_record_id: localStorage.media_bias_lexica_annotator_survey_record_id_key,
                  age: this.responses.age,
                  gender: this.responses.gender,
                  education: this.responses.education,
                  native_english_speaker: this.responses.native_english_speaker,
              }).then(response => {
                  this.overlay = false;
                  this.handleResponse(response);
              }, error => {
                  this.overlay = false;
                  this.$refs.snack.showSnackbar('error', 'Got nothing from server. Please check your internet connection or refresh the page.');
              })
          },
          handleResponse: function (response) {
              this.$router.replace({name: 'IdeologicalQuestions'});
          },
          recordChoice: function(question, choice) {
              if (!this.completedQuestions.includes(question)) {
                  this.completedQuestions.push(question);
                  this.responses[question.name] = choice.id;
              } else {
                  this.responses[question.name].push(choice.id);
              }
          },
          recordTextResponse: function(question, text) {
              if (!this.completedQuestions.includes(question)) {
                  this.completedQuestions.push(question);
                  this.responses[question.name] = text;
              } else {
                  this.responses[question.name] = text;
              }
          },
          incrementSlide: function () {
              setTimeout(() => {
                  if (this.cId < this.questions.length - 1) {
                      this.cId++;
                  }
              }, 0)
          },
          decrementSlide: function () {
              setTimeout(() => {
                  if (this.cId > 0) {
                      this.cId--;
                  }
              }, 0)
          },
      }
  }
</script>

<style scoped>

</style>



